<template>
	<div id="auth-loginBak">
		<public-header title="登录"></public-header>
		<div class="content">
			<div class="login-group">
				<div class="logo">
					<img :src="config.logo" alt="" class="img-100">
				</div>
				<div class="login-input">
					<i class="van-icon van-icon-contact"></i>
					<input v-model.number="mobile" type="text">
				</div>
				<div class="login-input">
					<i class="icon icon-lock"></i>
					<input v-model="password" type="password" class="pass-input">
					<router-link tag="div" :to="util.getUrl({path: '/pages/auth/forget'})" class="no-pass">忘记密码？</router-link>
				</div>
				<div class="login-btn-group">
					<van-button size="normal" block class="bg-info" :disabled="!islegal" @click="onSubmit">登录</van-button>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				config: {
					logo: '',
				},
				mobile: '',
				password: '',
				showPreLoading: true,
				islegal: false
			}
		},
		methods: {
			onLoad() {
				var that = this;
				var clerkInfo = that.util.getStorage('clerkInfo');
				if(clerkInfo && clerkInfo.token) {
					that.$router.replace(that.util.getUrl({path: '/pages/order/index'}));
					return;
				}
				getData({
					vue: that,
					url: 'manage/auth/login',
					data: {
						nosid: 1
					},
					autoAssign: true,
					variable: 'config',
					success: () => {
						that.islegal = true;
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.mobile) {
					that.util.$toast('请输入手机号');
					return;
				}
				if(!that.password) {
					that.util.$toast('请输入密码');
					return;
				}
				setData({
					vue: that,
					url: 'manage/auth/login',
					data: {
						mobile: that.mobile,
						password: that.password,
						nosid: 1,
					},
					success: function(res) {
						that.util.setStorage('clerkInfo', {token: res.clerk.token});
						let sids = res.sids;
						if(sids.length == 1) {
							that.util.setStorage('__sid', sids[0]);
							that.util.$toast('登录成功', that.util.getUrl({path: '/pages/order/index'}), 1000, 'replace');
							return;
						} else if(sids.length > 1) {
							that.util.$toast('登录成功', that.util.getUrl({path: '/pages/shop/select'}), 1000, 'replace');
							return;
						}
					},
					fail: function(res) {
						that.util.$toast(res.message);
						that.islegal = true;
					},
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#auth-loginBak .content{
		background-color: #fff;
	}
	#auth-loginBak .content .login-group{
		padding: 20px;
	}
	#auth-loginBak .login-group .logo{
		width: 60%;
		height: 90px;
		margin: 0 auto;
		padding-bottom: 20px;
	}
	#auth-loginBak .login-group .login-input{
		display: flex;
		height: 45px;
		line-height: 45px;
		border: 1px solid #DCE3E7;
		border-radius: 3px;
		padding: 10px 15px;
		margin-bottom: 15px;
		background-color: #F3F6F8;
	}
	#auth-loginBak .login-group .login-input .van-icon{
		font-size: 16px;
		height: 25px;
		line-height: 25px;
		color: #98A3AA;
		margin-right: 5px;
	}
	#auth-loginBak .login-group .login-input .icon{
		font-size: 18px;
		height: 25px;
		line-height: 25px;
		color: #98A3AA;
		margin-right: 5px;
	}
	#auth-loginBak .login-group .login-input input{
		overflow: hidden;
		width: 100%;
		color: #333;
		background-color: #F3F6F8;
	}
	#auth-loginBak .login-group .login-input .pass-input{
		width: 67%;
	}
	#auth-loginBak .login-group .login-input .no-pass{
		height: 25px;
		line-height: 25px;
		color: #33aafc;
		font-size: 15px;
	}
	#auth-loginBak .login-group .login-btn-group{
		margin-top: 30px;
	}
	#auth-loginBak .login-group .login-btn-group .login-btn{
		display: block;
		line-height: 45px;
		color: #fff;
		background-color: #33aafc;
		border: 1px solid #33aafc;
		border-radius: 3px;
		text-align: center;
	}
</style>


